import { MdEmail, MdLock } from 'react-icons/md'
import { useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";

import bannerImage from '../../assets/banner.png'
import { Button } from "../../components/Button"
import { Header } from "../../components/Header"
import { Input } from "../../components/Input"

import { api } from '../../services/api';
import {Column, Container, CriarText, EsqueciText, Row, SubtitleLogin, Title, TitleLogin, Wrapper } from './styles'

const schema = yup.object({
    email: yup.string().email('email no  vlido.').required('Campo obrigatrio!'),
    password: yup.string().min(3, 'No mnimo 3 caracteres!').required('Campo obrigatrio!'),
}).required();

const Login = () => {

        const navigate = useNavigate();

        const { control, handleSubmit, formState: { errors } } = useForm({
            resolver: yupResolver(schema),
            mode: 'onChange',
        });

const style = {color: "#8647AD"};

        console.group(errors);

        const onSubmit = async formData => {
            try {
                const { data } = await api.get(`users?email=${formData.email}&senha=${formData.password}`);
                if(data.length === 1) {
                    navigate('/feed')
                } else {
                    alert('Email ou senha invlido')
                }
            } catch {
                alert('Houve um erro, tente novamente.')
            }
        }



    return (<>
    <Header/>
    <Container>
        <Column>
            <Title>
                A plataforma para voc aprender com experts, dominar as principais tecnologias
                e entrar mais rpido nas empresas mais desejadas.
            </Title>
        </Column>
        <Column>
            <Wrapper>
                <TitleLogin>Faa seu Login</TitleLogin>
                <SubtitleLogin>Faa seu login e para acessar o seu progresso._</SubtitleLogin>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <Input name="email" errorMessage={errors?.email?.message} control={control} placeholder="E-mail" leftIcon={<MdEmail style={style} />} />
                    <Input name="password" errorMessage={errors?.password?.message} control={control} placeholder="Senha" type="password" leftIcon={<MdLock style={style} />} />
                    <Button title="Entrar" variant="secondary"  type="submit" />
                </form>
                <Row>
                    <EsqueciText><a href="/forgotpassword">Esqueci minha senha!</a></EsqueciText>
                    <CriarText><a href="/signup">Crie sua conta!</a></CriarText>
                </Row>
            </Wrapper>
        </Column>
    </Container>
    </>)
}

export { Login }